import Api from '@/services/Api'

export default {
    getAllBooks () {
        return Api().get('all-books')
    },
    getBookById (id) {
        return Api().get('book-by-id', {params: id})
    },
    getBooksByCategory (search) {
        //eslint-disable-next-line
        console.log('finding Category:', search)
        return Api().get('books-by-category', {params: {category : search}})
    }
}